/* Global Styles */
body {
    font-family: Arial, sans-serif;
    background-color: #75b3d6;
    margin: 0;
    padding: 0;
  }
  
  .container {
    max-width: 400px;
    margin: 50px auto;
    background-color: #f1eeee;
    padding: 20px;
    border-radius: 20px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    background:transparent;
    backdrop-filter:blur(10px);
    border: 2px solid #7e7b7b;
  }
  
  /* Header Styles */
  
  
  .header h1 {
    margin: 0;
    font-size: 40px;
    color: #f1f3f3;
  }
  
  .header nav {
    margin-top: 20px;
  }
  
  .header nav ul {
    list-style-type: none;
    padding: 0;
    
  }
  
  .header nav ul li {
    display: inline;
    margin: 10px;
  }
  
  .header nav ul li a {
    color: #ebeaea;
    text-decoration: none;
    font-weight: bold;
  }
  
  /* Form Styles */
  form {
    margin-top: 20px;
  }
  
  form label {
    display: block;
    margin-bottom: 8px;
  }
  
  form input {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  form button {
    width: 100%;
    padding: 10px;
    margin-top: 20px;
    background-color: #333;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  form button:hover {
    background-color: #555;
  }
  
  /* Link Styles */
  p {
    margin-top: 20px;
    text-align: center;
  }
  
  p a {
    color: #333;
    text-decoration: underline;
  }
  
  p a:hover {
    color: #555;
  }
  .login-container {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    border: 2px solid #7e7b7b;
    border-radius: 5px;
    background-color: #f9f9f9;
    background:transparent;
    backdrop-filter:blur(10px);
  }
  
  .login-container h2 {
    margin-bottom: 20px;
  }
  
  .login-container input {
    width: 100%;
    margin-bottom: 10px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .login-container button {
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .login-container button:hover {
    background-color: #0056b3;
  }
  